/*******************************************************************************
  CSS reset, and General styles
*******************************************************************************/
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

html {
  font-size: 10px; }

body {
  border: 0;
  color: #000;
  font-family: Helvetica, Arial, Verdana, Tahoma, sans-serif;
  font-weight: 300;
  vertical-align: baseline;
  width: 100%; }

ul,
ol {
  list-style: none; }

a {
  color: #1e1e1e;
  text-decoration: none; }
  a:hover {
    color: #1e1e1e;
    text-decoration: none; }
  a:visited {
    color: #1e1e1e; }

img {
  line-height: 0;
  vertical-align: middle; }

button,
input,
optgroup,
select,
textarea {
  font-family: inherit; }

button {
  background: transparent;
  border: 0;
  cursor: pointer;
  outline: 0; }

button[disabled] {
  cursor: not-allowed;
  opacity: 0.5; }

table {
  border-collapse: collapse; }

textarea {
  overflow: auto;
  resize: vertical; }

